.menu{
	&__list{
		display: flex;
		align-items: center;
	}

	&__item{
		margin-right: 20px;

		&:last-child{
			margin-right:0;
		}

		&--number{
			&:after{
				content:'15';
				display: inline-block;
				background-color: #f7a238;
				color: #fff;
				font-weight: 900;
				border-radius: 13px;
    			padding: 0 11px;
			}
		}
	}

	&__link{
		color: #fff;
		font-weight: 100;
		font-size: 16px;
		transition: .3s;

		&:hover{
			color:#ff0079;
		}

		&--pink{
			background-color: #ea3180;
			padding: 5px 8px;
			border-radius: 15px;

			&:hover{
				color:#fff;
			}
		}
	}
}