.slider{
	
	&__wrapper{
		display: flex;
		align-items: center;
	}

	&__list{
		display: flex;
		width: 100%;
	}

	&__item{
		margin-right: 30px;
		width: 50%;

		&:last-child{
			margin-right:0;
		}
	}

	&__avatar{
		height: 270px;
		margin-bottom: 44px;

		&-pic{
			height: 100%;
			width: 100%;
			object-fit: cover;
		}
	}

	&__controlls{
		display: inline-block;
		height: 70px;
		min-width: 70px;
		background-color: #e1e1e1;
		border-radius: 10px;
		transition: .4s;
		position: relative;
		margin-right: 30px;

		&:hover{
			background-color:#aaaaaa;
		}

		&:after{
			content: '';
			display: inline-block;
			height: 30px;
			width: 18px;
			background: url(../images/arrow.png)center center no-repeat;

			position: absolute;
			top: 50%;
			left: 50%;
			/*transform-origin: 50% 50%;*/
			transform: translate(-50%,-50%);
			z-index: 1;
		}

		&--next{
			margin-right: 0;
			margin-left: 30px;
			&:after{
				transform:translate(-50%,-50%) rotate(180deg);
			}
		}
	}

	&__description{
		&-item{
			display: flex;
			align-items: center;

			&:before{
				content:'';
				display: inline-block;
				height: 4px;
				width: 4px;
				background-color: #ff0079;
				border-radius: 50%;
				margin-right: 8px;
			}
		}
	}

	&__price{
		font-weight: 900;
		font-size: 28px;
		color: #ff0079;
		margin: 41px 70px 0 70px;
	    padding-top: 30px;
	    border-top: 2px solid #c5c5c5;
	}

	&__rating{
		display: flex;
		justify-content: space-between;
		margin-bottom: 7px;

		&-number{
			font-weight: 900;
			color: #ff0079;
			font-size: 21px;
		}

		&-stars{
			&_list{
				display: flex;
			}

			&_item{
				margin-right: 12px;

				&:last-child{
					margin-right:0;
				}
			}

			&_link{}
		}
	}

	&__marks{
		font-weight: 100;
		font-size: 16px;
		margin-bottom: 15px;
	}
}