.footer{
	padding: 70px 0 100px 0;

	&__row{
		width: 100%;
		margin-bottom: 44px;

		&:last-child{
			margin-bottom:0;
		}

		&--flex{
			display:flex;
			justify-content: space-between;
		}
	}

	&__list{
		display: flex;
		justify-content: space-between;
	}

	&__item{
		&-name{
			font-size: 16px;
    		font-weight: 100;
		}

		&-descr{
			font-weight: 900;
		    font-size: 16px;
		    color: #ff0079;
			display: inline-flex;
			align-items: center;

		    &:before{
		    	content:'';
		    	display: inline-block;
		    	background-repeat: no-repeat;
		    	background-position: center center;
		    	margin-right: 8px;
		    }

		    &--pynkt{
		    	&:before{
					height: 20px;
					width: 14px;
					background: url(../images/icon/pynkt.png);
		    	}
		    }

		    &--tel{
		    	&:before{
					height: 16px;
					width: 15px;
					background: url(../images/icon/tel.png);
		    	}
		    }

		    &--mess{
		    	&:before{
					height: 13px;
					width: 18px;
					background: url(../images/icon/mess.png);
		    	}
		    }
		}
	}

	&__info{
		&-text{
			font-weight: 100;
			font-size: 16px;
			color: #767676;
		}

		&-user{
			.button{
				font-size: 16px;
			}
		}
	}
}