.reviews{
	&__title{
		margin-bottom: 118px;
	}
	&__slider{
		.slider{
			&__item{
				/*border: 1px solid black;*/
				padding: 29px 30px;
				box-shadow: 0 0 12px #f2f2f2;
			}

			&__text{
				font-weight: 100;
				font-size: 20px;
				line-height: 30px;
				margin-bottom: 29px;
				font-style: italic;
			}

			&__icon{
				display: inline-block;
				height: 70px;
				width: 70px;
				border: 1px solid transparent;
				border-radius: 50%;
				overflow: hidden;
				margin-bottom: 3px;

				&-pic{
					height: 100%;
					width: 100%;
					object-fit: cover;
					object-position: 50% 15%;
				}
			}

			&__content{
				border-bottom: 1px solid #c5c5c5;
			    padding-bottom: 24px;
			    margin-bottom: 17px;
			}

			&__rating{
				margin-bottom: 31px;
			}
		}
	}
}