.answer-for-quest{
	&__title{
		margin-bottom: 90px;
	}

	&__list{
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		align-content: flex-start;
		align-self: flex-start;
	}

	&__content{
		margin-bottom: 4px;
		width: 48%;
		margin-right: 30px;

		&:nth-child(2n){
			margin-right: 0;
		}

		&--active{
			.content__title:after{
				transform:rotate(180deg);
			}
		}

		.content{
			&__title{
				background-color: #f9f9f9;
				display: flex;
				align-items: center;
				justify-content: space-between;
				width: 100%;

				&:after{
					content:'';
					display: inline-block;
					width: 24px;
					height: 13px;
					background: url(../images/arrow-pink.png) center center no-repeat;
				}
			}
		}
	}
}