.button{
	transition: .4s;

	&--callme{
		font-weight: 700;
		color: #ff0079;
		background: transparent;
		border:2px solid #ea3180;
		padding: 11px 8px;
		min-width: 150px;
		border-radius: 10px;

		&:hover{
			background-color: #ea3180;
			color: #fff;
			border-color: #ea3180;
		}
	}

	&--benefits{
		border-bottom: 1px solid #f9c8dd;
	}

	&--default{
		color: #fff;
		font-size: 20px;
		font-weight: 900;
		padding: 17px 0;
		min-width: 300px;
		text-align: center;
		border-radius: 10px;
		background: #f5a138; /* Old browsers */
		background: -moz-linear-gradient(top, #f5a138 0%, #e79734 33%, #e89834 33%, #d98e31 70%, #c7822d 100%);
		background: -webkit-linear-gradient(top, #f5a138 0%,#e79734 33%,#e89834 33%,#d98e31 70%,#c7822d 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to bottom, #f5a138 0%,#e79734 33%,#e89834 33%,#d98e31 70%,#c7822d 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	}
}