.contacts{
	width: 100%;
	
	&__map{
		background: url(../images/map.png) center center no-repeat;
		-webkit-background-size: auto;
		background-size: auto;
		width: 100%;
	}

	&__title{
		margin-bottom: 45px;

		.title{
			font-size: 20px;
		}
	}

	&__info{
		display: inline-block;
		background: rgba(#f9f9f9, 0.9);
		height: 100%;
		width: 465px;
		padding: 105px 60px 105px 70px;

		&-item{
			display: flex;
			margin-bottom: 34px;

			&:last-child{
				margin-bottom:0;
			}
		}

		&-name{
			font-size: 20px;
			font-weight: 900;
			color: #ea3180;
			margin-right: 6px;
		}

		&-text{
			font-size: 20px;
			font-weight: 100;
			line-height: 27px;
		}

		&-span{
			display: inline-flex;
			align-items: center;
			font-weight: 900;
			font-size: 16px;
			color: #ff0079;
			margin-top: 16px;

			&:before{
				content:'';
				display: inline-block;
				background: url(../images/people-icon.png)center center no-repeat;
				height: 24px;
				width: 12px;
				margin-right: 12px;
			}
		}

		&-button{
			margin-top: 31px;

			.button{
				width: 100%;
			}
		}
	}
}