.price-for-work{
	background-color: #f9f9f9;

	.container{}

	&__title{
		margin-bottom: 96px;
	}

	&__head{
		&-list{
			display: flex;
			align-items: center;
		}

		&-content{
			background-color: #fff;
			text-align: center;
			margin-right: 4px;
			padding: 21px 0;
			width: 33%;

			&:last-child{
				margin-right:0;
			}

			&--active{
				.content{
					&__title{
						color: #ff0079;
					}
				}
			}
		}
	}

	&__content{
		padding-top: 33px;
    	padding-left: 32px;
	}

	&__list{
		margin-top: 62px;
		display: flex;
	}

	&__item{
		width: 33%;
		margin-right: 30px;
		background-color: #fff;

		&:last-child{
			margin-right:0;
		}
	}

	&__price{
		display: flex;
		color: #ff0079;
		font-weight: 100;
		font-size: 20px;
		margin-top: 15px;
		margin-left: 33px;

		&-real{
			font-weight: 900;
		}

		&-unreal{
			font-weight: 100;
			text-decoration: line-through;
			margin-right: 17px;
		}
	}

	&__img{
		width: 100%;
		height: 200px;
		position: relative;
		margin-top: 26px;

		&-pic{
			height: 100%;
			width: 100%;
			object-fit: cover;
		}
	}

	&__popular{
		background-color: #f7a238;
		display: inline-flex;
		align-items: center;
		padding: 5px 12px;
		width: 140px;
		transform: rotate(-3deg) skew(-3deg);

		position: absolute;
		top: 6px;
    	right: -8px;
		z-index: 2;

		&-text{
			font-size: 16px;
			color: #fff;
			text-transform: uppercase;
			font-weight: 900;
		}
	}

	&__text{
		font-size: 16px;
		color: #8d8d8d;
		font-weight: 100;
		line-height: 24px;
		margin-top: 25px;
    	margin-left: 32px;
	}

	&__button{
		margin-top:26px;
		text-align: center;
	}
}