.benefits{

	.container{}

	&__title{
		margin-bottom: 80px;
	}

	&__list{
		display: flex;
		/*margin-bottom: -70px;*/
		flex-wrap: wrap;
	}

	&__item{
		margin-right: 40px;
	    margin-bottom: 70px;
	    width: 31%;

		&:nth-child(3n){
			margin-right:0;
		}

		&:last-child{
			margin-right:0;
		}
	}

	&__icon{
		display: inline-block;
		margin-bottom: 25px;
		height: 66px;
		position: relative;

		&-pic{
			max-width: 100%;
			max-height: 100%;
		}

		&-number{
			color: #ea3180;
			font-size: 12px;
			font-weight: 900;

			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
		}
	}

	&__content{
		margin-bottom: 14px;

		.content{
			&__descr{
				margin-top: 16px;
				font-size: 16px;
			}
		}
	}

	&__button{
		margin-top: auto;
	}
}