.intro{
	width: 100%;
	background: url(../images/intro-bg.png) center center no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
	
	height: 1px;
	min-height: 700px;

	&__wrapper{
		height: 100%;
		width: 100%;
		background-color: rgba(#000000, .6);
	}

	&__content{
		margin-top: 90px;
		display: inline-block;
    	width: 76%;
	}

	&__title{
		color: #fff;
		font-weight: 900;
		font-size: 48px;
		line-height: 58px;
	}

	&__description{
		font-size: 24px;
		font-weight: 100;
		color: #fff;
		margin-top: 27px;
    	line-height: 35px;

		span.pink{
			color: #ff0079;
			font-weight: 900;
		}
	}
}